$jl-font-family: Arial, Baskerville, monospace !default;
$jl-overlay-background: #fff !default;
$jl-overlay-opacity: 0.7 !default;
$jl-navigation-color: #000 !default;
$jl-caption-color: #fff !default;
$jl-caption-background: rgba(0, 0, 0, 0.8) !default;

$jl-counter-fontsize: 1rem !default;
$jl-caption-fontsize: 1rem !default;
$jl-close-fontsize: 3rem !default;

$jl-breakpoint-medium: 35.5em !default; // 568px, when 1em == 16px
$jl-breakpoint-large:	50em !default;	 // 800px, when 1em == 16px

$jl-arrow-fontsize-small:	2rem !default;
$jl-arrow-fontsize-medium: 3rem !default;
$jl-arrow-fontsize-large:	3rem !default;
$jl-img-border-small:	0 none !default;
$jl-img-border-medium: 0 none !default;
$jl-img-border-large:	0 none !default;
$jl-iframe-border-small:	0 none !default;
$jl-iframe-border-medium: 0 none !default;
$jl-iframe-border-large:	0 none !default;

body.hidden-scroll {
	overflow: hidden;
}
.jl-overlay {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: $jl-overlay-background;
	opacity: $jl-overlay-opacity;
	display: none;
	z-index: 1035;
}
.jl-wrapper {
	z-index: 1040;
	button {
		border: 0 none;
		background: transparent;
		font-size: 28px;
		padding: 0;
		cursor: pointer;
		&:hover {
			opacity: 0.7;
		}
	}

	.jl-close {
		display: none;
		position: fixed;
		right: 30px;
		top: 30px;
		z-index: 1060;
		margin-top: -14px;
		margin-right: -14px;
		height: 44px;
		width: 44px;
		line-height: 44px;
		font-family: $jl-font-family;
		color: $jl-navigation-color;
		font-size: $jl-close-fontsize;

		&:focus {
			outline: none;
		}
	}

	.jl-counter {
		display: none;
		position: fixed;
		top: 30px;
		left: 30px;
		z-index: 1060;
		color: $jl-navigation-color;
		font-size: $jl-counter-fontsize;
	}

	.jl-navigation {
		width: 100%;
		display: none;
		button {
			position: fixed;
			top: 50%;
			margin-top: -22px;
			height: 44px;
			width: 22px;
			line-height: 44px;
			text-align: center;
			display: block;
			z-index: 1060;
			font-family: $jl-font-family;
			color: $jl-navigation-color;
			&.jl-next {
				right: 5px;
				font-size: $jl-arrow-fontsize-small;
			}

			&.jl-prev {
				left: 5px;
				font-size: $jl-arrow-fontsize-small;
			}

			&:focus {
				outline: none;
			}

			@media (min-width: $jl-breakpoint-medium) {
				width: 44px;

				&.jl-next {
					right: 10px;
					font-size: $jl-arrow-fontsize-medium;
				}

				&.jl-prev {
					left: 10px;
					font-size: $jl-arrow-fontsize-medium;
				}
			}
			@media (min-width: $jl-breakpoint-large) {
				width: 44px;

				&.jl-next {
					right: 20px;
					font-size: $jl-arrow-fontsize-large;
				}

				&.jl-prev {
					left: 20px;
					font-size: $jl-arrow-fontsize-large;
				}
			}
		}
	}

	.jl-image {
		position: fixed;
		touch-action: none;
		z-index: 10000;
		img {
			margin: 0;
			padding: 0;
			display: block;
			border: $jl-img-border-small;
			@media (min-width: $jl-breakpoint-medium) {
				border: $jl-img-border-medium;
			}
			@media (min-width: $jl-breakpoint-large) {
				border: $jl-img-border-large;
			}

		}
		iframe {
			background: #000;
			border: $jl-iframe-border-small;
			@media (min-width: $jl-breakpoint-medium) {
				border: $jl-iframe-border-medium;
			}
			@media (min-width: $jl-breakpoint-large) {
				border: $jl-iframe-border-large;
			}
		}
		.jl-caption {
			padding: 10px;
			color: $jl-caption-color;
			background: $jl-caption-background;
			font-size: $jl-caption-fontsize;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;

			&.pos-top {
				bottom: auto;
				top: 0;
			}

			&.pos-outside {
				bottom: auto;
			}
		}

		.jl-download {
			display: none;
			position: absolute;
			bottom: 5px;
			right: 5px;
			color: $jl-navigation-color;
			z-index: 1060;
		}
	}
}

.jl-spinner {
	display: none;
	border: 5px solid #333;
	border-radius: 40px;
	height: 40px;
	left: 50%;
	margin: -20px 0 0 -20px;
	opacity: 0;
	position: fixed;
	top: 50%;
	width: 40px;
	z-index: 1007;
	animation: jl-pulsate 1s ease-out infinite;
}

.jl-scrollbar-measure {
	position: absolute;
	top: -9999px;
	width: 50px;
	height: 50px;
	overflow: scroll;
}

@keyframes jl-pulsate {
	0% {
		transform: scale(.1);
		opacity: 0.0;
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: scale(1.2);
		opacity: 0;
	}
}
