@import "legacy/mixins";

.image-choices-field.has-jetsloth-lazy .jetsloth-lazy {
	opacity: 0;
	transition: opacity 0.3s;
}
.image-choices-field.has-jetsloth-lazy .jetsloth-lazy-loaded {
	opacity: 1;
}

.image-choices-field {
	@include legacy_image_choices_field();
}
html[dir="rtl"] .image-choices-field .image-choices-choice {
	input[type="radio"],
	input[type="checkbox"] {
		left: unset;
		right: -9999px;
	}
}

.image-choices-field .image-choices-choice.image-choices-choice-other {
	input[type="text"] {
		//position: absolute;
		//width: calc(100% - 40px) !important;
		margin: 0 0 0 0;
		//left: 20px;
		//top: 50%;
		//transform: translateY(-50%);
		//text-align: center;
		//border: none;
		//background-color: transparent;
		//z-index: 1;
	}
	.image-choices-choice-image-wrap {
		background-color: #ECEFED;
	}
}

.image-choices-field .gf_other_wrap {
	position: absolute;
	width: 150%;
	max-width: 90vw;
	margin: 0;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: #fff;
	padding: 36px 10px 20px 10px;
	z-index: 99;
	box-shadow: 0 0 20px 0 rgba(0,0,0,0.2);

	&:not(.active) {
		pointer-events: none;
		visibility: hidden;
		.gf_other_close {
			display: none;
		}
	}
}

.image-choices-field .gf_other_close {
	position: absolute;
	right: 5px;
	top: 5px;
	width: 24px;
	height: 24px;
	opacity: 0.3;
	&:before,
	&:after {
		position: absolute;
		left: 10px;
		content: ' ';
		height: 24px;
		width: 2px;
		background-color: #000;
	}
	&:before {
		transform: rotate(45deg);
	}
	&:after {
		transform: rotate(-45deg);
	}
}


// {all_quiz_results} merge tag
.gquiz-image-choices-field {

	.gquiz-image-choices {
		list-style: none;
		//display: flex;
		display: block;
		text-align: center;
	}
	.gquiz-image-choices > li,
	.gquiz-image-choices-choice {
		//flex: 1;
		display: inline-block;
		vertical-align: top;
		margin: 0 10px 20px;
		text-align: center;
		position: relative;
	}
	.gquiz-image-choices > li > img,
	.gquiz-image-choices-choice > img {
		position: absolute;
		right: 0;
		bottom: -15px;
	}

	.gquiz-image-choices-choice-image {
		display: inline-block;
		width: 80px;
		height: 80px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50%;
	}
	.gquiz-image-choices-choice-text {
		display: block;
		font-size: 12px;
	}

}

.image-choices-choice {
	position: relative;
}


/* Orbital / Theme Framework overrides */

.gform-theme--framework {
	.gfield--type-choice.image-choices-field:not([class*="gf_list_"]) {
		.gfield_checkbox,
		.gfield_radio {
			flex-direction: row;
			flex-wrap: wrap;
		}
	}
	.gfield--type-choice.image-choices-field {
		.gchoice {
			display: inline-block;
			margin-right: 0 !important;
		}
		.gfield_checkbox > :last-child:where(:not(.gchoice)),
		.gfield_radio > :last-child:where(:not(.gchoice)) {
			align-self: center;
		}
	}
}

@media (min-width: 640px) {
	.gform-theme--foundation {
		.gfield.image-choices-field[class*="gf_list_"] {
			.gfield_checkbox,
			.gfield_radio {
				grid-column-gap: var(--gf-field-choice-spacing , var(--gform-theme-field-choice-spacing) );
			}
		}
	}
}


